.container {
  background-color: #444D58;
  box-sizing: border-box;
  padding: 25px;
  min-height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;

}

.comments {
  overflow: auto;
  width: 75%;
  max-width: 700px;
  min-width: 300px;
  height: 300px;
  background-color: #C3C0C0;
  border-radius: 30px;
}

.comments h2 {
  font-weight: 300;
}

.singleComment {
  padding: 10px 20px;
  margin-bottom: 10px;
  display: flex;
}

.singleCommentContent {
  width: 75%;
  min-width: 590px;
  background-color: #444D58;
  color: #C4C4C4;
  border-radius: 25px;
  padding: 20px;
  display: inline-flex;
}

.userImage {
  height: 60px;
  margin-right: 10px;
  border-radius: 50%;
}


.singleCommentContent h3 {
  margin: 0;
  padding-right: 5px;
  font-size: 12px;
  font-style: italic;
  font-weight: 1000 ;
  color: white;
}

.singleCommentContent p {
  font-size: 12px;
  margin: 0;
}

.singleCommentButtons {
  padding-top: 5px;
}

.time {
  font-size: 10px;
  padding-left: 5px;
  padding-right: 5px;
  color: #999;
}

.singleCommentButtons a {
  margin: 0px 3px;;
  padding-top: 10px;
  cursor: pointer;
  font-size: 10px;
  color: #385997;
  letter-spacing: 0.05em;
}

.form {
  overflow: auto;
  width: 75%;
  max-width: 700px;
  min-width: 300px;
  border-radius: 30px;
}

.form form {
  display: flex;
  padding: 15px 0;
  flex-wrap: wrap;
}

.form input {
  margin-top: 10px;
  height: 80px;
  padding-left: 10px;
  border: 1px solid #c1c1c1;
  background-color: #C3C0C0;
  margin-right: 10px;
  border-radius: 25px;
  width: 100%;
}

.form input:first-child {
  width: 30%;
  height: 30px;
}             

.form button {
  margin-top: 10px;
  padding: 8px 30px;
  color: black;
  font-weight: bold;
  border-radius: 25px;
  border: 1px solid #999;
  background-color: #C3C0C0;
}

.form button:hover {
  cursor: pointer;
  color: #385997;
  border-color: #385997;
}

.idea {
  width: 75%;
  max-width: 700px;
  min-width: 300px;
  height: 200px;
  border-radius: 30px;
  text-align: left;
}

.idea h2{
  padding-top: 10px;
  margin-bottom: 5px;
}

.idea h4 {
  padding-top: 10px;
  margin-bottom: 5px;
}

.idea_name {
  background-color: #30363E;
  width: 100%;
  height: 50%;
  border-top-right-radius: 30px;
  border-top-left-radius: 30px;
  padding-left: 20px;
  color: white;
}

.description {
  background-color: #89969F;
  width: 100%;
  height: 50%;
  border-bottom-right-radius: 30px;
  border-bottom-left-radius: 30px;
  padding-left: 20px;
}

